




























import Vue from 'vue'
import moment from 'moment'
import { Brother } from '~/components/BrotherCard.vue'
import { DataCodeError } from '~/plugins/Errors'

export default Vue.extend({
  data () {
    const brothers: Brother[] = []
    return {
      brothers,
      about: '',
      todos: ''
    }
  },
  mounted () {
    Promise.all([
      this.fetchBrother()
    ]).catch((error) => {
      this.$sentry.captureException(error)
    })
    this.$axios.$get('https://md.qiniu.amemachif.ioit.pub/TODOS.md')
      .then((todos) => {
        this.todos = this.$md.render(todos)
      })
    this.$axios.$get('https://md.qiniu.amemachif.ioit.pub/about_project.md')
      .then((about) => {
        this.about = this.$md.render(about)
      })
  },
  methods: {
    async fetchBrother () {
      try {
        const res = await this.$axios.get('https://api.amemachif.com:2333/brother')
        if (res.data.code === 20000) {
          this.brothers = []
          res.data.data.forEach((b: any) => {
            this.brothers.push({
              id: b.id,
              avatar: b.avatar,
              name: b.name,
              signature: b.signature,
              updateTime: moment(b.update_time * 1000).format('YYYY-MM-DD HH:mm:ss')
            })
          })
        } else {
          throw new DataCodeError('fetchBrother', res.data)
        }
      } catch (error) {
        this.$sentry.captureException(error)
      }
    }
  }
})
